import React, { Suspense, useState } from "react";
import { Button, Card, CardBody, CardHeader, CardTitle, Table } from "react-bootstrap";
import SeeMoreModal from "../../layout/common/SeeMoreModal"; 
import { baseUrl, manageSpace } from "../../../../../utils";
const LeaderbordSkeleton = React.lazy(() =>
  import("../../layout/skeleton/LeaderbordSkeleton")
);

const LeaderbordPopup = React.lazy(() =>
  import("../../layout/popup/LeaderbordPopup")
);

const LeaderBoard = (props) => {

  console.log(props,'propspropspropsdsdf')

  const { leaderBoard, loader } = props;

  const [show, setShow] = useState(false)

  const handlePopup = event => {  
    setShow(event)
  }

  return (
   <>
    <Card className="sw-card-custom gutter-b card-shadow mb-5">
      <CardHeader className="px-5 pt-5 border-0 pb-0">
        <CardTitle className="mb-0 d-flex align-items-center justify-content-between">
          <h6 class="card-label font-weight-bolder m-0">Leader Board</h6>
          { leaderBoard.total>5 ?  <Button variant="link" className="p-0" onClick={()=>handlePopup(true)}>See More</Button> : null }
        </CardTitle>
      </CardHeader>
      <CardBody className="py-5 px-0">
        <Table responsive>
          <thead>
            <tr>
              <th style={{ width: "10%" }}>
                S No.
              </th>
              <th style={{ width: "20%" }}>
                Name
              </th>
              <th style={{ width: "15%" }}>
                No. Followres
              </th>
              <th style={{ width: "15%" }}>
                No. of Posts
              </th>
              <th style={{ width: "30%" }}>
                Social Accout
              </th>
              <th style={{ width: "10%" }}></th>
            </tr>
          </thead>
          <tbody>
            { loader ? <Suspense fallback={<></>}> <LeaderbordSkeleton/> </Suspense> : leaderBoard && Object.keys(leaderBoard.data)
              ? leaderBoard.data.map((data,index) => {
                  return (
                    <tr className="border-top">
                      <td>
                        <strong>{index+1}.</strong>
                      </td>
                      <td>
                        <div className="d-flex align-items-center">
                          <div className="symbol symbol-35 rounded-circle overflow-hidden symbol-light me-3 con_user_img cursor-pointer">
                              <img
                                src={data.image}
                                className="img-fluid"
                                width={"20px"}
                                height={"20px"}
                             />
                          </div>
                          <div class="d-flex text-left flex-column conn_user_info cursor-pointer">
                            <p class="text-dark mb-0 font-weight-boldest overflow-hidden text-nowrap text-ellipsis author_name text-capitalize">
                              {data.author.username}
                            </p>
                            <p class="m-0">
                              <small class="text-muted overflow-hidden text-nowrap text-ellipsis">
                                {`@${manageSpace(data.author.username)}`}
                              </small>
                            </p>
                          </div>
                        </div>
                      </td>
                      <td>
                        <strong>{data.author.follower}</strong>
                      </td>
                      <td>
                        <strong>{data.postCount}</strong>
                      </td>
                      <td>
                        <i className={`fab ${data.network.icon} font-size-h2`}></i>
                      </td>
                      <td>
                        <div className="conn_status_wrap">
                          <button onClick={() =>window.open(data.links)}
                            type="button"
                            className="btn btn-primary btn-min-w btn-sm rounded"
                          >
                            <span className="position-relative">
                              {" "}
                              <svg
                                xmlns="http://www.w3.org/2000/svg"
                                width="22.213"
                                height="18"
                                viewBox="0 0 22.213 18"
                                className="h-75 align-self-end w-20 me-1"
                              >
                                <g
                                  id="noun-talk-2902544"
                                  transform="translate(-98.007 -75.786)"
                                >
                                  <path
                                    id="Path_6701"
                                    data-name="Path 6701"
                                    d="M103.686,84.417a1.084,1.084,0,1,1-.317-.766A1.084,1.084,0,0,1,103.686,84.417Zm2.9-1.084h0a1.084,1.084,0,1,0,.766.317,1.084,1.084,0,0,0-.766-.317Zm3.979,0h0a1.084,1.084,0,1,0,.766.317,1.084,1.084,0,0,0-.766-.317Zm9.659-5.711v8.172a1.839,1.839,0,0,1-1.616,1.823l.625,2.7a.617.617,0,0,1-1,.608l-3.07-2.625v.2a1.838,1.838,0,0,1-1.836,1.836h-9.463L100,93.638a.617.617,0,0,1-1-.608l.625-2.7A1.838,1.838,0,0,1,98.007,88.5V80.331a1.838,1.838,0,0,1,1.836-1.836h3.226v-.873a1.838,1.838,0,0,1,1.836-1.836h13.478a1.838,1.838,0,0,1,1.836,1.836Zm-6.3,2.709a.6.6,0,0,0-.6-.6H99.843a.6.6,0,0,0-.6.6V88.5a.6.6,0,0,0,.6.6h.552a.617.617,0,0,1,.6.756l-.375,1.621,2.607-2.229a.618.618,0,0,1,.4-.148h9.691a.6.6,0,0,0,.6-.6Zm5.063-2.709a.6.6,0,0,0-.6-.6H104.906a.6.6,0,0,0-.6.6V78.5h9.017a1.838,1.838,0,0,1,1.836,1.836v6.35l2.448,2.093-.375-1.621a.617.617,0,0,1,.6-.756h.553a.6.6,0,0,0,.6-.6Z"
                                    fill="#ffffff"
                                  ></path>
                                </g>
                              </svg>{" "}
                              Connect 
                            </span>
                          </button>
                        </div>
                      </td>
                    </tr>
                  );
                })
              : <div className="py-10">
              <div className="mb-3 text-center">
                  <img className="img-fluid graph-bg" src={baseUrl(`dashboard/analytic/image-placeholder.svg`)} width={"80px"} height={"80px"}/>
              </div>
              <div className="">
                  <h5 class="text-gray-dark text-center">No Data Available</h5>
              </div>
          </div>}

          </tbody>
        </Table>
      </CardBody>
     </Card>

   {leaderBoard.total>5 ? 
    <SeeMoreModal size={'lg'} showHide={show} handlePopup={handlePopup} title={'Leader Bord'}>
      <Suspense fallback={<></>}>
         <LeaderbordPopup {...props} />
       </Suspense>  
    </SeeMoreModal> : null }
    </>    
  );
};

export default React.memo(LeaderBoard);
